	if(_GRIDROWS>1 && _GRIDCOLS > 1){
		_XMIN = gridData[0][0][0];
		_YMIN = gridData[0][0][1];
		_DELTX = gridData[1][0][0] - gridData[0][0][0];
		_DELTY = gridData[0][1][1] - gridData[0][0][1];
	}
