var Monitor = require('@ali/g2-monitor');
// 延迟发送请求
setTimeout(function () {
  if (Monitor.tracking) {
    var m = new Monitor();
    m.log({
      g2: true,
      version: '2.3.1',
      page_type: 'page'
    });
  }
}, 3000);



