//取小于当前值的
function arrayFloor(values,value){
  var length = values.length;
  if (length === 0) {
    return NaN;
  }


  var pre = values[0];

  if(value < values[0]){
    return NaN;
  }

  if(value >= values[length - 1]){
    return values[length - 1];
  }
  for (var i = 1; i < values.length; i++) {
    if(value < values[i]){
      break;
    }
    pre = values[i];
  }

  return pre;
}
//大于当前值的第一个
