/**
 * @fileOverview labels的入口，决定如何选择对应的文本排布方式
 * @author dxq613@gmail.com
 */

'use strict';

var GeomLabels = require('./geom-labels');
var CircleLabels = require('./circle-labels');
var PieLabels = require('./pie-labels');

var Labels = {
  getLabelsClass: function(coordType) {
    var rst = GeomLabels;
    if (coordType === 'polar' || coordType === 'plus') {
      rst = CircleLabels;
    } else if (coordType === 'theta') {
      rst = PieLabels;
    }

    return rst;
  }
};

module.exports = Labels;



